import React, { useEffect, useMemo, useState } from 'react'
import styled from 'styled-components'
import { useFormatMessage } from 'react-intl-hooks';
import dayjs from 'dayjs'
import utc from 'dayjs/plugin/utc'

import { Box, Text } from 'rebass'
import TokenLogo from '../TokenLogo'
import { CustomLink, } from '../Link'

import { formattedNum, formattedPercent } from '../../utils'
import { withRouter } from 'react-router-dom'
import { OVERVIEW_TOKEN_BLACKLIST } from '../../constants'
import FormattedName from '../FormattedName'
import LocalLoader from "../LocalLoader";
import InfiniteScroll from "react-infinite-scroller";

dayjs.extend(utc)

const List = styled(Box)`
  -webkit-overflow-scrolling: touch;
`

const DashGrid = styled.div`
  display: flex;
  line-height: 35px;
  padding: 0 10px;
  margin-bottom: 5px;
  background: ${({theme})=> theme.bg11};
  border-radius: 8px;
  flex-direction: column;
`

const ListWrapper = styled.div`
  display:flex;
  flex-direction:column;
  align-items:center;
`

const ClickableText = styled(Text)`
  color: ${ ({ theme }) => theme.text10 };
  text-align: start;
  user-select: none;
`

const DataText = styled.div`
  display: flex;
  justify-content: space-between;
  color: ${ ({ theme }) => theme.white };
`
const ValueText = styled.div`
  color: ${({theme})=> theme.text7};
  font-weight: 500;
  font-size: 14px;
  font-family: Helvetica;
`

// @TODO rework into virtualized list
function TopTokenList({ tokens, loadMore, itemMax = 10 }) {
  const t = useFormatMessage()
  const [ hasMore, setHasMore ] = useState(true)
  const [dataList, setDataList] = useState([])

  const formattedTokens = useMemo(() => {
    return (
      tokens &&
      Object.keys(tokens)
        .filter(key => {
          return !OVERVIEW_TOKEN_BLACKLIST.includes(key)
        })
        .map(key => tokens[key])
    )
  }, [tokens])

  const filteredList = useMemo(() => {
    return (
      formattedTokens &&
      formattedTokens
        .sort((a, b) => {
          return parseFloat(a['totalLiquidityUSD']) > parseFloat(b['totalLiquidityUSD']) ? -1 : 1
        })
    )
  }, [formattedTokens])

  const ListItem = ({ item }) => {
    return (
        <DashGrid>
            <CustomLink to={ '/token/' + item.id }>
              <DataText area="name" fontWeight="500">
                <div style={{ display: 'flex', justifyContent: 'start', lineHeight: '35px' }}>
                  <TokenLogo address={item.id} size={'20px'} />
                  <FormattedName text={item.name} maxCharacters={16} adjustSize={true} link={true}/>
                </div>
                <ValueText style={{display: 'flex'}}>
                  {formattedNum(item.priceUSD, true)}
                  <div style={{paddingLeft: '10px'}}>{formattedPercent(item.priceChangeUSD)}</div>
                </ValueText>
              </DataText>
              <DataText>
                <ClickableText area="liq">{t({id: 'liquidity'})}</ClickableText>
                <ValueText area="liq" style={ { justifyContent: 'flex-end' } }>{formattedNum(item.totalLiquidityUSD, true)}</ValueText>
              </DataText>
              <DataText>
                <ClickableText area="vol">{t({id: 'volume24'})}</ClickableText>
                <ValueText area="vol" style={ { justifyContent: 'flex-end' } }>{formattedNum(item.oneDayVolumeUSD, true)}</ValueText>
              </DataText>
            </CustomLink>
        </DashGrid>
    )
  }
  // 
  useEffect(() => {
    const tokenList =
      filteredList &&
      filteredList.map((item, index) => {
          return (
            <div key={ index }>
              <ListItem key={ index } item={item} />
            </div>
          )
        })
    setDataList(tokenList)
  }, [filteredList])

  const handleInfiniteOnLoad = () => {
    if (formattedTokens.length === 0) return;
    if (!hasMore) return;
    if (dataList.length >= formattedTokens.length) {
      setHasMore(false);
      return;
    }
  };
  return (
    <ListWrapper>
      <List p={0} style={{ width: '100%', height: '440px', overflow: 'auto' }}>
        {dataList.length === 0 ? <LocalLoader /> : (
          <InfiniteScroll
              initialLoad={false}
              pageStart={0}
              loadMore={handleInfiniteOnLoad}
              hasMore={hasMore}
              useWindow={false}
          >
            {dataList}
          </InfiniteScroll>
        )}
      </List>
    </ListWrapper>
  )
}

export default withRouter(TopTokenList)
