import React, { useEffect, useState } from 'react'
import { withRouter, useHistory } from 'react-router-dom'
import { useFormatMessage } from 'react-intl-hooks';
import 'feather-icons'
import styled from 'styled-components'
import Panel from '../components/Panel'
import { PageWrapper, ContentWrapper } from '../components/index'
import { AutoRow, RowBetween, RowFixed } from '../components/Row'
import { AutoColumn } from '../components/Column'
import PairChart from '../components/PairChart'
import TxnList from '../components/TxnList'
import SupportText from '../components/SupportText'
import Loader from '../components/LocalLoader'
import { formattedNum, formattedPercent, localNumber } from '../utils'
import { useColor } from '../hooks'
import { usePairData, usePairTransactions } from '../contexts/PairData'
import CopyHelper from '../components/Copy'
import DoubleTokenLogo from '../components/DoubleLogo'
import TokenLogo from '../components/TokenLogo'
import { Hover } from '../components'

import FormattedName from '../components/FormattedName'
import SwapImg from '../assets/swap.png'
import TxnType from "../components/TxnType";
import Bottom from "../components/Bottom";
import Back from '../assets/back.png'
import Back2 from '../assets/back2.png'
import { useDarkModeManager } from "../contexts/LocalStorage";
import { judgeClient } from '../utils/bridge'
const DashboardWrapper = styled.div`
  width: 100%;
  margin-top:20px;
`
const HeadText = styled.div`
  font-size: 17px;
  font-family: PingFangSC, PingFangSC-Regular;
  font-weight: 400;
  color: ${ ({ theme }) => theme.text7 };
  text-align: center;
  margin-top: 10px;
`
const HeadLeft = styled(HeadText)`
  font-size: 25px;
  text-align: left;
  top: 16px;
  position: absolute;
  left:8px;
`
const HeaderBox = styled.div`
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  background: ${({ theme }) => theme.white};
  border-radius: 15px;
  margin: 10px 0;
  color: ${({ theme }) => theme.black};
`
const HeaderTitle = styled.div`
  font-size: 14px;
  font-family: PingFangSC, PingFangSC-Regular;
  font-weight: 400;
  margin-bottom: 5px;
  color: #666666;
`
const HeaderValue = styled.div`
  font-size: 14px;
  color: #333333;
  font-family: PingFangSC, PingFangSC-Medium;
  font-weight: 500;
`
const InfoBox = styled.div`
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  background: ${({ theme }) => theme.bg11};
  border-radius: 15px;
  margin: 10px 0;
  color: ${({ theme }) => theme.black};
`
const InfoTitle = styled.div`
  font-size: 14px;
  color: ${ ({ theme }) => theme.text10 };
  font-family: PingFangSC, PingFangSC-Medium;
  font-weight: 400;
`
const InfoValue = styled.div`
  font-size: 14px;
  color: ${({ theme }) => theme.text7 };
  font-family: PingFangSC, PingFangSC-Medium;
  font-weight: 500;
`
const Line = styled.div`
  width: 1px;
  height: 15px;
  background: #999999;
  borderRadius: 1px;
  `
const SwapText = styled.div`
  font-size: 16px;
  font-family: Helvetica;
  text-align: left;
  margin-bottom: 20px;
  color: ${ ({ theme }) => theme.text7 };
  `
const Link = styled.div`
color: ${({ color, theme }) => (color ? color : theme.link)};
`
function PairPage({ pairAddress, history }) {
  const t = useFormatMessage()
  const {
    token0,
    token1,
    reserve0,
    reserve1,
    reserveUSD,
    trackedReserveUSD,
    oneDayVolumeUSD,
    volumeChangeUSD,
    oneDayVolumeUntracked,
    volumeChangeUntracked,
    liquidityChangeUSD,
    oneDayTxns,
    txnChange
  } = usePairData(pairAddress)

  useEffect(() => {
    document.querySelector('body').scrollTo(0, 0)
  }, [])
  const router = useHistory()
  const transactions = usePairTransactions(pairAddress)
  const backgroundColor = useColor(pairAddress)
  const [darkMode] = useDarkModeManager()

  // transactions
  const txnChangeFormatted = formattedPercent(txnChange)

  // liquidity
  const liquidity = trackedReserveUSD
    ? formattedNum(trackedReserveUSD, true)
    : reserveUSD
      ? formattedNum(reserveUSD, true)
      : '-'
  const liquidityChange = formattedPercent(liquidityChangeUSD)

  // volume	  // volume
  const volume =
    oneDayVolumeUSD || oneDayVolumeUSD === 0
      ? formattedNum(oneDayVolumeUSD === 0 ? oneDayVolumeUntracked : oneDayVolumeUSD, true)
      : oneDayVolumeUSD === 0
        ? '$0'
        : '-'

  // mark if using untracked volume
  const [usingUtVolume, setUsingUtVolume] = useState(false)
  useEffect(() => {
    setUsingUtVolume(oneDayVolumeUSD === 0 ? true : false)
  }, [oneDayVolumeUSD])

  const volumeChange = formattedPercent(!usingUtVolume ? volumeChangeUSD : volumeChangeUntracked)

  // get fees	  // get fees
  const fees =
    oneDayVolumeUSD || oneDayVolumeUSD === 0
      ? usingUtVolume
        ? formattedNum(oneDayVolumeUntracked * 0.003, true)
        : formattedNum(oneDayVolumeUSD * 0.003, true)
      : '-'

  // rates
  const token0Rate = reserve0 && reserve1 ? formattedNum(reserve1 / reserve0) : '-'
  const token1Rate = reserve0 && reserve1 ? formattedNum(reserve0 / reserve1) : '-'

  // formatted symbols for overflow
  const formattedSymbol0 = token0?.symbol.length > 6 ? token0?.symbol.slice(0, 5) + '...' : token0?.symbol
  const formattedSymbol1 = token1?.symbol.length > 6 ? token1?.symbol.slice(0, 5) + '...' : token1?.symbol

  const [txFilter, setTxFilter] = useState('All')
  const [ swapStatus, setSwapStatus ] = useState(true)
  useEffect(() => {
    window.scrollTo({
      behavior: 'smooth',
      top: 0
    })
  }, [])

  return (
    <PageWrapper>
      <ContentWrapper>
        <DashboardWrapper>
          <AutoColumn gap="40px" style={{ marginBottom: '20px' }}>
            <div style={{ display: 'flex', justifyContent: 'center'}}>
              <HeadLeft onClick={()=>{router.push('/home?type=2')}}><img src={darkMode? Back : Back2} alt="Back" width={'22px'}/></HeadLeft>
              <HeadText>
                {token0 && token1 && (
                    <div style={{display: 'flex'}}>
                      <DoubleTokenLogo style={{top: '0px'}} a0={token0?.id || ''} top={'0'} a1={token1?.id || ''} size={20} margin={true} />
                      {token0.symbol}-{token1.symbol}
                    </div>
                )}{' '}
              </HeadText>
            </div>
          </AutoColumn>
          <RowBetween>
            <SwapText>
              {token0 && token1 ? ( swapStatus ? `1 ${formattedSymbol0} = ${token0Rate} ${formattedSymbol1}`:`1 ${formattedSymbol1} = ${token1Rate} ${formattedSymbol0}` ) : '-'}
              <img src={ SwapImg } onClick={ () => {
                setSwapStatus(!swapStatus)
              } } alt="" style={ { width: '20px', paddingLeft: '10px', verticalAlign: ' sub' } }/>
            </SwapText>
          </RowBetween>
          <HeaderBox>
            <HeaderTitle style={{color:'#333333'}}>{t({id: 'pooledToken'})}</HeaderTitle>
            <RowBetween>
              <Hover onClick={() => history.push(`/token/${token0?.id}`)} fade={true}>
                <AutoRow gap="4px">
                  <TokenLogo size={'20px'} address={token0?.id} />
                  <HeaderValue>
                    <RowFixed>
                      {reserve0 ? formattedNum(reserve0) : ''}{' '}
                      <FormattedName style={{color: '#333333'}} text={token0?.symbol ?? ''} maxCharacters={8} margin={true} />
                    </RowFixed>
                  </HeaderValue>
                </AutoRow>
              </Hover>
              <Line/>
              <Hover onClick={() => history.push(`/token/${token1?.id}`)} fade={true}>
                <AutoRow gap="4px">
                  <TokenLogo size={'20px'} address={token1?.id} />
                  <HeaderValue>
                    <RowFixed>
                      {reserve1 ? formattedNum(reserve1) : ''}{' '}
                      <FormattedName style={{color: '#333333'}} text={token1?.symbol ?? ''} maxCharacters={8} margin={true} />
                    </RowFixed>
                  </HeaderValue>
                </AutoRow>
              </Hover>
            </RowBetween>
          </HeaderBox>
          <HeaderBox>
            <HeaderTitle>{t({id: 'liquidity'})}</HeaderTitle>
            <RowBetween style={{marginBottom: '10px'}}>
              <HeaderValue style={{fontSize: '24px'}}>{liquidity}</HeaderValue>
              <HeaderValue>{liquidityChange}</HeaderValue>
            </RowBetween>
            <HeaderTitle>{t({id: 'volume24'})}</HeaderTitle>
            <RowBetween style={{marginBottom: '10px'}}>
              <HeaderValue style={{fontSize: '24px'}}>{volume}</HeaderValue>
              <HeaderValue>{volumeChange}</HeaderValue>
            </RowBetween>
            <HeaderTitle>{t({id: 'txn24'})}</HeaderTitle>
            <RowBetween style={{marginBottom: '10px'}}>
              <HeaderValue style={{fontSize: '24px'}}>{oneDayTxns ? localNumber(oneDayTxns) : oneDayTxns === 0 ? 0 : '-'}</HeaderValue>
              <HeaderValue>{txnChangeFormatted}</HeaderValue>
            </RowBetween>
            <HeaderTitle>{t({id: 'fee24'})}</HeaderTitle>
            <RowBetween style={{marginBottom: '10px'}}>
              <HeaderValue style={{fontSize: '24px'}}>{fees}</HeaderValue>
              <HeaderValue>{volumeChange}</HeaderValue>
            </RowBetween>
          </HeaderBox>
          <PairChart
              address={pairAddress}
              color={backgroundColor}
              base0={reserve1 / reserve0}
              base1={reserve0 / reserve1}
          />
          <InfoBox>
            <RowBetween style={{marginBottom: '10px'}}>
              <InfoTitle style={{fontSize: '16px'}}>{t({id: 'pairInfo'})}</InfoTitle>
              <Link color={backgroundColor} onClick={()=>{
                const client = judgeClient()
                if (client === 'pc') {
                  window.location.href = `https://etherscan.io/address/${pairAddress}`
                } else {
                  window.JSBridge.invoke('openBrowerser', {
                    url: `https://etherscan.io/address/${pairAddress}` // url
                  })
                }
              }}>{t({id: 'viewEtherscan'})}</Link>
            </RowBetween>
            <RowBetween style={{marginBottom: '10px'}}>
              <InfoTitle>{t({id: 'pair'})}</InfoTitle>
              <InfoValue style={{ display:'flex'}}>
                {token0?.symbol}-{token1?.symbol}
                <CopyHelper toCopy={token0?.symbol + '-' + token1?.symbol}/>
              </InfoValue>
            </RowBetween>
            <RowBetween style={{marginBottom: '10px'}}>
              <InfoTitle>{t({id: 'address'})}</InfoTitle>
              <InfoValue style={{ display:'flex'}}>
                {pairAddress.slice(0, 6) + '...' + pairAddress.slice(38, 42)}
                <CopyHelper toCopy={pairAddress}/>
              </InfoValue>
            </RowBetween>
            <RowBetween style={{marginBottom: '10px'}}>
              <InfoTitle>{token0?.symbol} {t({id: 'address'})}</InfoTitle>
              <InfoValue style={{display:'flex'}}>
                {token0 && token0.id.slice(0, 6) + '...' + token0.id.slice(38, 42)}
                <CopyHelper toCopy={token0 && token0.id}/>
              </InfoValue>
            </RowBetween>
            <RowBetween style={{marginBottom: '10px'}}>
              <InfoTitle >{token1?.symbol} {t({id: 'address'})}</InfoTitle>
              <InfoValue style={{display:'flex'}}>
                {token1 && token1.id.slice(0, 6) + '...' + token1.id.slice(38, 42)}
                <CopyHelper toCopy={token0 && token1.id}/></InfoValue>
            </RowBetween>
          </InfoBox>
          <div>
            <TxnType active={txFilter} setActive={setTxFilter}></TxnType>
            <Panel style={{ marginTop: '1.5rem', padding: '0px 0px 20px 0', border: 'none', borderRadius: '4px' }}>
              {transactions ? <TxnList transactions={transactions} type={txFilter} /> : <Loader />}
            </Panel>
          </div>
        </DashboardWrapper>
      </ContentWrapper>
      <SupportText margin={true}/>
      <Bottom address0={ token0?.id } address1={ token1?.id } type={2}/>
    </PageWrapper >
  )
}

export default withRouter(PairPage)
