import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { useFormatMessage } from 'react-intl-hooks';
import dayjs from 'dayjs'
import utc from 'dayjs/plugin/utc'

import { formattedNum } from '../../utils'

import { Box } from 'rebass'
import ArrowImg from '../../assets/arrow.png'
import PlusImg from '../../assets/plus.png'
import InfiniteScroll from "react-infinite-scroller";
import TokenLogo from "../TokenLogo";

dayjs.extend(utc)

const List = styled(Box)`
  -webkit-overflow-scrolling: touch;
`

const DashGrid = styled.div`
  display: flex;
  height: 180px;
  margin-bottom: 5px;
  background: ${({theme})=> theme.bg17};
  border-radius: 8px;
  flex-direction: column;
`
const DashContent = styled.div`
  display: flex;
  height: 105px;
  padding: 15px 15px;
  background: ${({theme})=> theme.bg15};
  border-radius: 8px;
  flex-direction: column;
`

const DataTitle = styled.div`
  color: ${({theme})=> theme.text7};
`
const DataText = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`
const FlexStart = styled.div`
  display: flex;
  justify-content: space-start;
  align-items: center;
`
const ValueText = styled.div`
  color: ${({theme})=> theme.text7};
  font-family: PingFangSC, PingFangSC-Medium;
  font-weight: 500;
  padding-left: 10px;
`
const AccountText = styled.div`
  color: ${({theme})=> theme.text10};
  font-weight: 400;
  font-family: PingFangSC, PingFangSC-Regular;
`
const Line = styled.div`
  width: 90%;
  height: 1px;
  background: ${({theme})=> theme.bg16};
  margin-left: 6px;
`
const NoData = styled.div`
  text-align: center;
  padding: 20px
  color: ${({theme})=> theme.text7};
`
const SORT_FIELD = {
  VALUE: 'amountUSD',
  AMOUNT0: 'token0Amount',
  AMOUNT1: 'token1Amount',
  TIMESTAMP: 'timestamp'
}

const TXN_TYPE = {
  ALL: 'All',
  SWAP: 'Swaps',
  ADD: 'Adds',
  REMOVE: 'Removes'
}

// @TODO rework into virtualized list
function TxnList({ transactions, type, itemMax=10 }) {
  const t = useFormatMessage()
  // page state
  const [page, setPage] = useState(1)
  const [dataList, setDataList] = useState([])
  const [ hasMore, setHasMore ] = useState(true)
  // sorting
  const [sortedColumn] = useState(SORT_FIELD.TIMESTAMP)
  const [filteredItems, setFilteredItems] = useState()
  const [txFilter, setTxFilter] = useState(TXN_TYPE.ALL)

  function getTransactionType(event) {
    switch (event) {
      case TXN_TYPE.ADD:
        return t({id: 'add'})
      case TXN_TYPE.REMOVE:
        return t({id: 'remove'})
      case TXN_TYPE.SWAP:
        return t({id: 'swap'})
      default:
        return ''
    }
  }

  function formatTime (unix) {
    const now = dayjs()
    const timestamp = dayjs.unix(unix)
    const inSeconds = now.diff(timestamp, 'second')
    const inMinutes = now.diff(timestamp, 'minute')
    const inHours = now.diff(timestamp, 'hour')
    const inDays = now.diff(timestamp, 'day')
    if (inHours >= 24) {
      return `${inDays}${inDays === 1 ? t({id: 'tian'}) : t({id: 'tians'})}${t({id: 'ago'})}`
    } else if (inMinutes >= 60) {
      return `${inHours}${inHours === 1 ? t({id: 'hour'}) : t({id: 'hours'})}${t({id: 'ago'})}`
    } else if (inSeconds >= 60) {
      return `${inMinutes}${inMinutes === 1 ? t({id: 'minute'}) : t({ id: 'minutes'})}${t({id: 'ago'})}`
    } else {
      return `${inSeconds}${inSeconds === 1 ? t({id: 'second'}) : t({id: 'seconds'})}${t({id: 'ago'})}`
    }
  }

  useEffect(() => {
    setTxFilter(type)
    setPage(1)
    setHasMore(true);
  }, [type])
  // parse the txns and format for UI
  useEffect(() => {
    if (transactions && transactions.mints && transactions.burns && transactions.swaps) {
      let newTxns = []
      if (transactions.mints.length > 0) {
        transactions.mints.map(mint => {
          let newTxn = {}
          newTxn.hash = mint.transaction.id
          newTxn.timestamp = mint.transaction.timestamp
          newTxn.type = TXN_TYPE.ADD
          newTxn.token0Amount = mint.amount0
          newTxn.token1Amount = mint.amount1
          newTxn.account = mint.to
          newTxn.token0Symbol = mint.pair.token0.symbol
          newTxn.token1Symbol = mint.pair.token1.symbol
          newTxn.amountUSD = mint.amountUSD
          newTxn.token0Address = mint.pair.token0.id
          newTxn.token1Address = mint.pair.token1.id
          return newTxns.push(newTxn)
        })
      }
      if (transactions.burns.length > 0) {
        transactions.burns.map(burn => {
          let newTxn = {}
          newTxn.hash = burn.transaction.id
          newTxn.timestamp = burn.transaction.timestamp
          newTxn.type = TXN_TYPE.REMOVE
          newTxn.token0Amount = burn.amount0
          newTxn.token1Amount = burn.amount1
          newTxn.account = burn.sender
          newTxn.token0Symbol = burn.pair.token0.symbol
          newTxn.token1Symbol = burn.pair.token1.symbol
          newTxn.amountUSD = burn.amountUSD
          newTxn.token0Address = burn.pair.token0.id
          newTxn.token1Address = burn.pair.token1.id
          return newTxns.push(newTxn)
        })
      }
      if (transactions.swaps.length > 0) {
        transactions.swaps.map(swap => {
          const netToken0 = swap.amount0In - swap.amount0Out
          const netToken1 = swap.amount1In - swap.amount1Out

          let newTxn = {}

          if (netToken0 < 0) {
            newTxn.token0Symbol = swap.pair.token0.symbol
            newTxn.token1Symbol = swap.pair.token1.symbol
            newTxn.token0Amount = Math.abs(netToken0)
            newTxn.token1Amount = Math.abs(netToken1)
          } else if (netToken1 < 0) {
            newTxn.token0Symbol = swap.pair.token1.symbol
            newTxn.token1Symbol = swap.pair.token0.symbol
            newTxn.token0Amount = Math.abs(netToken1)
            newTxn.token1Amount = Math.abs(netToken0)
          }

          newTxn.hash = swap.transaction.id
          newTxn.timestamp = swap.transaction.timestamp
          newTxn.type = TXN_TYPE.SWAP

          newTxn.amountUSD = swap.amountUSD
          newTxn.account = swap.to
          newTxn.token0Address = swap.pair.token0.id
          newTxn.token1Address = swap.pair.token1.id
          return newTxns.push(newTxn)
        })
      }
      const filtered = newTxns.filter(item => {
        if (txFilter !== TXN_TYPE.ALL) {
          return item.type === txFilter
        }
        return true
      })
      setFilteredItems(filtered)
    }
  }, [transactions, txFilter])

  useEffect(() => {
    setPage(1)
  }, [txFilter])

  const filteredList =
    filteredItems &&
    filteredItems
      .sort((a, b) => {
        return parseFloat(a[sortedColumn]) > parseFloat(b[sortedColumn]) ? -1 : 1
      })
      // .slice(ITEMS_PER_PAGE * (page - 1), page * ITEMS_PER_PAGE)

  const ListItem = ({ item }) => {
    if (item.token0Symbol === 'WETH') {
      item.token0Symbol = 'ETH'
    }

    if (item.token1Symbol === 'WETH') {
      item.token1Symbol = 'ETH'
    }

    return (
      <DashGrid>
        <DataText style={{ height: '45px',lineHeight: '45px',paddingLeft: '15px'}}>
          <DataTitle>
              {getTransactionType(item.type)}
          </DataTitle>
        </DataText>
        <DashContent>
          <FlexStart>
            <TokenLogo address={item.token0Address} size="20px"/>
            <ValueText>
              {formattedNum(item.token0Amount) + ' '}{' '}{item.token0Symbol}
            </ValueText>
          </FlexStart>
          <FlexStart>
            <ValueText style={{padding: '5px 0'}}>
              {
                item.type === 'Swaps' ? <img src={ArrowImg} alt="" style={{width: '10px', padding: '0px 10px', marginLeft: '-6px', verticalAlign:' sub'}}/> :
                    <img src={PlusImg} alt="" style={{width: '16px', marginLeft: '-10px', padding: '0px 10px'}}/>
              }
            </ValueText>
            <Line/>
          </FlexStart>
          <FlexStart>
            <TokenLogo address={item.token1Address} size="20px"/>
            <ValueText>
              {formattedNum(item.token1Amount) + ' '}{' '}{item.token1Symbol}
            </ValueText>
          </FlexStart>
          <DataText style={{paddingTop: '20px'}}>
            <AccountText>
              {t({id: 'account'})}{item.account && item.account.slice(0, 6) + '...' + item.account.slice(38, 42)}
            </AccountText>
            <AccountText>
              {formatTime(item.timestamp)}
            </AccountText>
          </DataText>
        </DashContent>
      </DashGrid>
    )
  }

  // 
  useEffect(() => {
    const txnList =
        filteredList &&
        filteredList.slice(itemMax * (page - 1), page * itemMax)
            .map((item, index) => {
              return (
                  <div key={(page - 1) * itemMax + index + 1}>
                    <ListItem key={(page - 1) * itemMax + index + 1} item={item} />
                  </div>
              )
            })

    if (page === 1) {
      txnList && setDataList(txnList)
    } else {
      setDataList(L => {
        return [...L, ...txnList]
      })
    }
  }, [itemMax, filteredList, page])

  const handleInfiniteOnLoad = () => {
    if (filteredItems.length === 0) return;
    if (!hasMore) return;
    if (dataList.length >= filteredItems.length) {
      setHasMore(false);
      return;
    }
    setPage(page + 1)
  };
  return (
    <>
      {dataList.length > 0 ? (
          <List p={0} style={{ width: '100%', maxHeight: '553px', overflow: 'auto' }}>
            <InfiniteScroll
                initialLoad={false}
                pageStart={0}
                loadMore={handleInfiniteOnLoad}
                hasMore={hasMore}
                useWindow={false}
            >
              {dataList}
            </InfiniteScroll>
          </List>
      ) : (
          <NoData>{t({id: 'noData'})}</NoData>
      )}

    </>
  )
}

export default TxnList
