import React from 'react'
import styled from 'styled-components'
import { useFormatMessage } from 'react-intl-hooks';
import { TYPE } from "../../Theme";
import { RowFixed } from "../Row";

const TXN_TYPE = {
    ALL: 'All',
    SWAP: 'Swaps',
    ADD: 'Adds',
    REMOVE: 'Removes'
}

const SortText = styled.button`
  cursor: pointer;
  font-weight: ${({ active }) => (active ? 500 : 400)};
  margin-right: 30px !important;
  border: none;
  border-bottom: 2px solid ${({ active, theme }) => (active ? theme.bg8 : 'none')};
  background-color: transparent;
  font-size: 16px;
  padding: 0 0 5px;
  color: ${({ active, theme }) => (active ? theme.bg8 : theme.text10)};
  outline: none;
`
const TxnType = ({ active, setActive })=> {
    const t = useFormatMessage()
    return (
        <div>
            <TYPE.main fontSize={'16px'} style={{ margin: '10px 0 15px',
                fontFamily: 'PingFangSC, PingFangSC-Medium',
                fontWeight: '500' }}>
                {t({id: 'transactions'})}
            </TYPE.main>
            <RowFixed area="txn" gap="10px" pl={4} style={{ paddingBottom: '20px' }}>
                <SortText onClick={() => { setActive(TXN_TYPE.ALL) }} active={active === TXN_TYPE.ALL}>{t({id: 'all'})}</SortText>
                <SortText onClick={() => { setActive(TXN_TYPE.SWAP) }} active={active === TXN_TYPE.SWAP}>{t({id: 'swap'})}</SortText>
                <SortText onClick={() => { setActive(TXN_TYPE.ADD) }} active={active === TXN_TYPE.ADD}>{t({id: 'add'})}</SortText>
                <SortText onClick={() => { setActive(TXN_TYPE.REMOVE) }} active={active === TXN_TYPE.REMOVE}>{t({id: 'remove'})}</SortText>
            </RowFixed>
        </div>
    )
}


export default TxnType
