import React, { useState } from 'react'
import 'feather-icons'
import { withRouter, useHistory } from 'react-router-dom'
import { useFormatMessage } from 'react-intl-hooks'
import styled from 'styled-components'
import Panel from '../components/Panel'
import TokenLogo from '../components/TokenLogo'
import Loader from '../components/LocalLoader'
import { RowBetween } from '../components/Row'
import { AutoColumn } from '../components/Column'
import TxnList from '../components/TxnList'
import TxnType from '../components/TxnType'
import TokenChart from '../components/TokenChart'
import { formattedNum, formattedPercent, localNumber } from '../utils'
import { useTokenData, useTokenTransactions, useTokenPairs } from '../contexts/TokenData'
import { TYPE } from '../Theme'
import { useDataForList } from '../contexts/PairData'
import { useEffect } from 'react'
import { PageWrapper, ContentWrapper } from '../components'
import SupportText from "../components/SupportText";
import Bottom from "../components/Bottom";
import Back from "../assets/back.png";
import Back2 from "../assets/back2.png";
import { useDarkModeManager } from "../contexts/LocalStorage";

const DashboardWrapper = styled.div`
  width: 100%;
  margin-top:20px;
`

const HeadText = styled.div`
  font-size: 17px;
  font-family: PingFangSC, PingFangSC-Regular;
  font-weight: 400;
  color: ${ ({ theme }) => theme.text7 };
  text-align: center;
  margin-top: 10px;
`
const HeadLeft = styled(HeadText)`
  font-size: 25px;
  text-align: left;
  top: 18px;
  position: absolute;
  left: 8px;
`

const HeaderBox = styled.div`
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  background: ${({ theme }) => theme.white};
  border-radius: 15px;
  margin: 10px 0;
  color: ${({ theme }) => theme.black};
`
const HeaderTitle = styled.div`
  font-size: 12px;
  font-family: PingFangSC, PingFangSC-Medium;
  font-weight: 500;
  margin-bottom: 10px;
`
const HeaderValue = styled.div`
  font-size: 18px;
  font-family: Montserrat, Montserrat-SemiBold;
  font-weight: SemiBold;
`
function TokenPage({ address }) {
  const t = useFormatMessage()
  const {
    symbol,
    priceUSD,
    oneDayVolumeUSD,
    totalLiquidityUSD,
    volumeChangeUSD,
    oneDayVolumeUT,
    volumeChangeUT,
    priceChangeUSD,
    liquidityChangeUSD,
    oneDayTxns,
    txnChange
  } = useTokenData(address)

  useEffect(() => {
    document.querySelector('body').scrollTo(0, 0)
  }, [])

  const router = useHistory()

  const allPairs = useTokenPairs(address)

  // pairs to show in pair list
  // eslint-disable-next-line
  const fetchedPairsList = useDataForList(allPairs)

  // all transactions with this token
  const transactions = useTokenTransactions(address)

  // price
  const price = priceUSD ? formattedNum(priceUSD, true) : ''
  const priceChange = priceChangeUSD || priceChangeUSD === 0 ? formattedPercent(priceChangeUSD) : ''

  // volume
  const volume =
    oneDayVolumeUSD || oneDayVolumeUSD === 0
      ? formattedNum(oneDayVolumeUSD === 0 ? oneDayVolumeUT : oneDayVolumeUSD, true)
      : oneDayVolumeUSD === 0
        ? '$0'
        : '-'

  // mark if using untracked volume
  const [usingUtVolume, setUsingUtVolume] = useState(false)
  const [darkMode] = useDarkModeManager()
  useEffect(() => {
    let isUnmounted = false;
    if (!isUnmounted) {
      setUsingUtVolume(oneDayVolumeUSD === 0 ? true : false)
    }
    return () => {
      isUnmounted = true;
    }
  }, [oneDayVolumeUSD])

  const volumeChange = formattedPercent(!usingUtVolume ? volumeChangeUSD : volumeChangeUT)

  // liquidity
  const liquidity = totalLiquidityUSD ? formattedNum(totalLiquidityUSD, true) : totalLiquidityUSD === 0 ? '$0' : '-'
  const liquidityChange = formattedPercent(liquidityChangeUSD)

  // transactions
  const txnChangeFormatted = formattedPercent(txnChange)


  // format for long symbol
  const LENGTH = 10
  const formattedSymbol = symbol?.length > LENGTH ? symbol.slice(0, LENGTH) + '...' : symbol

  const [txFilter, setTxFilter] = useState('All')

  useEffect(() => {
    window.scrollTo({
      behavior: 'smooth',
      top: 0
    })
  }, [])

  return (
    <PageWrapper>
      <ContentWrapper>
        <DashboardWrapper>
          <AutoColumn gap="40px" style={{ marginBottom: '20px' }}>
            <div style={{ display: 'flex', justifyContent: 'center'}}>
              <HeadLeft onClick={()=>{router.push('/home?type=3')}}><img src={darkMode? Back : Back2} alt="Back" width={'22px'}/></HeadLeft>
              <HeadText>
                <div style={{display: 'flex'}}>
                   <TokenLogo address={address} size="20px"/>
                    {formattedSymbol ? `(${formattedSymbol})` : ''}
                </div>
              </HeadText>
            </div>
          </AutoColumn>
          <RowBetween>
            <div style={{display: 'flex', alignItems:'center'}}>
              <TYPE.main fontSize={'1.5rem'} fontWeight={500} style={{ marginRight: '1rem' }}>
                {price}
              </TYPE.main>
              {priceChange}
            </div>
          </RowBetween>
          <HeaderBox>
            <HeaderTitle>{t({id: 'liquidity'})}</HeaderTitle>
            <RowBetween style={{marginBottom: '10px'}}>
              <HeaderValue style={{fontSize: '24px'}}>{liquidity}</HeaderValue>
              <HeaderValue>{liquidityChange}</HeaderValue>
            </RowBetween>
            <HeaderTitle>{t({id: 'volume24'})}</HeaderTitle>
            <RowBetween style={{marginBottom: '10px'}}>
              <HeaderValue style={{fontSize: '24px'}}>{volume}</HeaderValue>
              <HeaderValue>{volumeChange}</HeaderValue>
            </RowBetween>
            <HeaderTitle>{t({id: 'txn24'})}</HeaderTitle>
            <RowBetween style={{marginBottom: '10px'}}>
              <HeaderValue style={{fontSize: '24px'}}>{oneDayTxns ? localNumber(oneDayTxns) : oneDayTxns === 0 ? 0 : '-'}</HeaderValue>
              <HeaderValue>{txnChangeFormatted}</HeaderValue>
            </RowBetween>
          </HeaderBox>
          <TokenChart address={address} color='#F5202C' base={priceUSD} />
          <div>
            <TxnType active={txFilter} setActive={setTxFilter}></TxnType>
            <Panel style={{ marginTop: '1.5rem', padding: '0px 0px 20px 0',  border: 'none', borderRadius: '4px' }}>
              {transactions ? <TxnList transactions={transactions} type={txFilter} /> : <Loader />}
            </Panel>
          </div>
        </DashboardWrapper>
      </ContentWrapper>
      <SupportText margin={true}/>
      <Bottom address0={ address } type={3}/>
    </PageWrapper>
  )
}

export default withRouter(TokenPage)
