import React from 'react'
import styled from 'styled-components'
import { useFormatMessage } from 'react-intl-hooks';
import { getPoolLink, getSwapLink } from "../../utils";

const Footer = styled.div`
    position: fixed;
    bottom: 10px;
    left: -12px;
    width: 100%;
`
const Text = styled.div`
    display: flex;
    justify-content: space-between;
    margin: 0 12px;
`
const Button = styled.div`
  background: ${({theme}) => theme.bg8};
  color: ${({theme}) => theme.white};
  min-width: fit-content;
  width:150px;
  height:40px;
  line-height: 40px;
  text-align: center;
  border-radius: 4px;
  white-space: nowrap;
`
const Bottom = ({address0, address1, type})=> {
    const t = useFormatMessage()
    function toPool() {
        window.location.href = getPoolLink(address0, address1) + `?url=${window.location.href.split('#')[0]}#/home/?type=${type}`
    }
    function toSwap() {
        window.location.href = getSwapLink(address0, address1) + `&url=${window.location.href.split('#')[0]}#/home/?type=${type}`
    }
    return (
        <Footer>
            <Text>
                <Button onClick={toPool}>{t({id: 'marketMakers'})}</Button>
                <Button onClick={toSwap}>{t({id: 'trade'})}</Button>
            </Text>
        </Footer>
    )
}


export default Bottom
